import * as $ from 'jquery';
import './tablesaw/tablesaw.stackonly.jquery';
import './tablesaw/tablesaw-init';

import {
    StandardSlick,
    initSliders
} from './sliders';

const slickSliders = [];

window.addEventListener('DOMContentLoaded', () => {
    const cardSlider = document.querySelector('.slider-cards');
    if (cardSlider) {
        slickSliders.push(new StandardSlick(cardSlider, {
            rows: 0,
            arrows: true,
            appendArrows: '.slider-cards__arrows',
            slidesToShow: 1,
            slidesToScroll: 1,
            mobileFirst: true,
            infinite: true,
            prevArrow: '.slider-cards__arrows .arrow--prev',
            nextArrow: '.slider-cards__arrows .arrow--next',
            variableWidth: false,
            centerMode: false,
            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 5
                    }
                },
                {
                    breakpoint: 759,
                    settings: {
                        slidesToShow: 2
                    }
                }
            ]
        }));
    }

    initSliders(slickSliders);

    $(window).on('resize orientationchange', () => {
        initSliders(slickSliders);
    });
});